var api = {
    env: ENV,

    get: function (api) {
        var res = service.api[ENV][api];
        if(!res){
            console.warn('No the api: ' + api);
            return;
        }

        res = service.root[ENV]+res;

        return res;
    },

    getRoot: function () {
        return service.root[ENV];
    }

}



