var Vue = require('vue');
var Api = require('./api.js');
var bFinished = false;

//$.ajax()
$.ajaxPrefilter( function( options, originalOptions, jqXHR ) {
    //httpmock
    if( !/^http/.test(Api.getRoot()) ){
        // var o = require(originalOptions.url);
        require('./mock_api/'+originalOptions.url);
    }

    
});

// ajax
$.ajaxSetup({

    // loading
    beforeSend: function() {
        bFinished = false;
        setTimeout(function () {
            //100,loading
            if(!bFinished){
                Vue.$$app.$root.$refs.loading.show = true;
            }
        }, 100);
    },
    dataFilter: function (response) {
        bFinished = true;
        //100,loading
        Vue.$$app.$root.$refs.loading.show = false;

        var data = JSON.parse(response);
        var msg = data.msg;
        switch( data.code ){
            case 200:
                return JSON.stringify(data);
                break;
            case 401:
                
                //
                if( Api.env === 'publish' || Api.env === 'test' ){
                    location.href="http://dap-admin.vip.vip.com/integration.php?c=home&a=login&backUrl="+location.href;
                }else{
                    // alert('');
                }
                return JSON.stringify(data);
                break;
            case 400:
                alert(msg);
                return JSON.stringify(data);
                break;
            case 404:
                alert(msg);
                return JSON.stringify(data);
                break;
            case 406:
                alert(msg);
                return JSON.stringify(data);
                break;
            case 407:
                alert('');
                return JSON.stringify(data);
                break;
            case 500:
                alert(msg);
                return JSON.stringify(data);
                break;
            case 1000:
                alert(msg);
                return JSON.stringify(data);
                break;
            case 5000:
                alert(msg);
                return JSON.stringify(data);
                break;
            default:
                alert(msg);
                return JSON.stringify(data);
                break;
        }

    },
    
    // ,
    error: function (res){
        alert('');
    },
});


//ajax fail, .fail(fn)
$(document).ajaxError(function() {
    //150beforeSend100
    setTimeout(function () {
        Vue.$$app.$root.$refs.loading.show = false;
    }, 150);
});

